function formatDate(d, fmt) {
  let fix = typeof d === 'string' ? d.replace(/-/g, '/') : d
  let date = new Date(fix)
  var o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    S: date.getMilliseconds(), // 
    D: '' + ['', '', '', '', '', '', ''][date.getDay() - 1] // 
  }
  var format = fmt
  if (/(y+)/.test(format))
    format = format.replace(
      RegExp.$1,
      String(date.getFullYear()).substr(4 - RegExp.$1.length)
    )
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length === 1
          ? o[k]
          : ('00' + o[k]).substr(String(o[k]).length)
      )
    }
  }
  return format
}

function relativeDate(v) {
  var now = new Date()
  var time = new Date(v)
  var span = now - time
  var day = Math.floor(span / 86400000)
  var hour = Math.floor(span / 3600000)
  var minute = Math.floor(span / 60000)
  // var second = Math.floor(span / 1000)

  if (day > 1) {
    if (day <= 3) {
      return `${day.toString()}`
    } else {
      return this.formatDate(time, 'yyyy-MM-dd hh:mm')
    }
  } else if (day > 0 && day <= 1) {
    return `${day.toString()}`
  } else if (hour > 0) {
    return `${hour.toString()}`
  } else if (minute > 0) {
    return `${minute.toString()}`
  }
  return ''
}

export default { formatDate, relativeDate }



